///    - .js
import React, { useState } from "react";
import $ from "jquery";
import "./css/member.css";
import { Link, useNavigate } from "react-router-dom";
import { clearData, initData } from "./fns/fnMem"; 


/* 
    [  : Hook -  ? ]
    1.  -    
               
            !
    2. 
        1)   import  -> useState
        2) useState()  
        : 
             = useState()
        :
            const [,set] = useState()
            -> set    !
            -> set() :    !( )
        3) 
            - useState     
            -  set   
            useState     
              !
        4) 
            -       
                   !
*/

//    /////////
function jqFn() {
    $(() => {}); //////// jQB ///////////
} ////////////// jQFn ///////////

function Member() {
    //  :      !
    //                !

    // [      : useNavigate ]
    // 1. Link     !
    // ->  ?  useNavigate
    // 2. import  : import {useNavigate} from "react-router-dom";
    // 3.  : 
    //  = useNavigate()
    // -> ()

    //    
    const goRoute = useNavigate();

    // [  useState   ]
    // [ 1.   ]
    // 1. 
    const [userId, setUserId] = useState("");
    // 2. 
    const [pwd, setPwd] = useState("");
    // 3. 
    const [chkPwd, setChkPwd] = useState("");
    // 4. 
    const [userName, setUserName] = useState("");
    // 5. 
    const [email, setEmail] = useState("");

    // [ 2.   ]
    // ->  :   (false)
    // 1. 
    const [userIdError, setUserIdError] = useState(false);
    // 2. 
    const [pwdError, setPwdError] = useState(false);
    // 3. 
    const [chkPwdError, setChkPwdError] = useState(false);
    // 4. 
    const [userNameError, setUserNameError] = useState(false);
    // 5. 
    const [emailError, setEmailError] = useState(false);

    // [    ]
    const msgId = [
        "User ID must contain a minimum of 5 characters",
        "This ID is already in use!",
        "That's a great ID!",
    ];
    //  
    const [idMsg, setIdMsg] = useState(msgId[0]);

    // [   ] // -> fns/fnMem.js 
    // const clearData = () => {
    //     localStorage.clear();
    //     console.log(" !");
    // }; /////////// clearData //////////////

    // [  ! ] /// -> fns/fnMem.js 
    // const initData = () => {

    //     //   "mem-data" null !
    //     if (localStorage.getItem("mem-data") === null) {
    //         localStorage.setItem(
    //             "mem-data",
    //             `
    //                 [
    //                     {
    //                         "idx": "1",
    //                         "uid":"tomtom",
    //                         "pwd":"1111",
    //                         "unm":"Tom",
    //                         "eml":"tom@gmail.com"
    //                     }
    //                 ]
    //             `
    //         );
    //     }
    // }; ///////////// initData /////////////////

    // [ 3.    ]
    // 1.   
    const changeUserId = (e) => {
        // e - 
        // 1.   ( !)
        const valid = /^[A-Za-z0-9+]{5,}$/;

        // 2.   : e.target ->   
        console.log(e.target.value);

        // 3.   if
        // :   true? ! false()
        // .test() ->    
        // : true  false / false  true
        if (valid.test(e.target.value)) {
            //    
            initData();

            //      
            let memData = localStorage.getItem("mem-data");
            console.log(":", memData);
            //  null
            if (memData) {
                //     
                //    (!)
                memData = JSON.parse(memData);
                console.log(":", memData);

                //    false 
                let isOK = true;

                // !
                memData.forEach((v) => {
                    //    !
                    if (v["uid"] === e.target.value) {
                        console.log(v["uid"]);
                        // 
                        setIdMsg(msgId[1]);
                        //  
                        setUserIdError(true);
                        //  
                        isOK = false;
                    } ////// if /////
                }); ///////// forEach //////////////

                //   !
                if (isOK) {
                    console.log("");
                    // ( )
                    setIdMsg(msgId[0]);
                    //  
                    setUserIdError(false);
                } /////////// if : isOK /////////
            } ///////// if ////////////////////
            else {
                console.log("DB !!!");
            } ////////// else /////////////////

            // setUserIdError(false); // !
        } ////// if ///////////
        else setUserIdError(true); // !

        // 4.  useerId     !
        setUserId(e.target.value);
    }; /////////////// changeUserId ////////////////

    // 2.   
    const changePwd = (e) => {
        // e - 
        // 1.  ( !)
        const valid = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

        // 2.   : e.target ->   
        console.log(e.target.value);

        // 3.   if
        // :   true? ! false()
        // .test() ->    
        // : true  false / false  true
        if (valid.test(e.target.value)) setPwdError(false); // !
        else setPwdError(true); // !

        // 4.  useerId     !
        setPwd(e.target.value);
    }; ///////////// changePwd ///////////////////

    // 3.   
    const changeChkPwd = (e) => {
        // 1.    
        if (pwd === e.target.value) setChkPwdError(false); // !
        else setChkPwdError(true); // !

        // 2.  
        setChkPwd(e.target.value);
    }; ////////////// changeChkPwd /////////////////

    // 4.  
    const changeUserName = (e) => {
        // 1.  
        if (e.target.value !== "") setUserNameError(false);
        else setUserNameError(true);

        // 2.  
        setUserName(e.target.value);
    }; ////////////// changeUserName /////////////////

    // 5.   ///////////////////////
    const changeEmail = (e) => {
        // 1.  
        const valid =
            /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;

        // 1.  
        if (valid.test(e.target.value)) setEmailError(false);
        else setEmailError(true);

        // 2.  
        setEmail(e.target.value);
    }; ////////////// changeEmail /////////////////

    // 6.     /////////////
    const totalValid = () => {
        //   (     !)
        if (!userId) setUserIdError(true);
        if (!pwd) setPwdError(true);
        if (!chkPwd) setChkPwdError(true);
        if (!userName) setUserNameError(true);
        if (!email) setEmailError(true);

        // :
        // 1.  
        // 2.     false
        //  2  treu 
        if (
            userId &&
            pwd &&
            chkPwd &&
            userName &&
            email &&
            !userIdError &&
            !pwdError &&
            !chkPwdError &&
            !userNameError &&
            !emailError
        )
            return true;
        else return false; //    false !
    }; ////////////// totalValid ////////////////

    // 7.   ///////////////
    const onSubmit = (e) => {
        //   !
        e.preventDefault();

        console.log("!");

        //    ////
        if (totalValid()) {
            // alert(" !");

            //  
            let memData = localStorage.getItem("mem-data");

            console.log(memData);

            //   
            memData = JSON.parse(memData);

            console.log(memData);

            //  
            let newObj = {
                idx: memData.length + 1,
                uid: userId,
                pwd: pwd,
                unm: userName,
                eml: email,
            };

            //   :    -> push()
            memData.push(newObj);

            //  
            console.log(memData);

            //  
            localStorage.setItem("mem-data", JSON.stringify(memData));

            //   (!)
            // useNavigate !
            $(".sbtn").text("   !");
            setTimeout(() => {
                goRoute('/login');                
            }, 1500);

        } /// if ////
        //  ////////////////
        else {
            // alert(" !");
        } /// else /////
    }; ///////////// onSubmit ////////////////

    return (
        <div className="outbx">
            {/*  */}
            <section className="membx">
                <h2 onClick={clearData}><img src="./images/svg/l-point.svg" alt="lottemember"/></h2>
                <form method="post" action="process.php">
                    <ul>
                        <li>
                            {/* 1. */}
                            <label><img src="./images/svg/id.svg" alt=""/></label>
                            <input
                                type="text"
                                maxLength="20"
                                placeholder="Please enter your ID"
                                value={userId}
                                onChange={changeUserId}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                //   idMsg !
                                userIdError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            {idMsg}
                                        </small>
                                    </div>
                                )
                            }

                            {
                                // " " !
                                //  false !
                                //   msgId  
                                //  : userId  
                                // userId  false !
                                !userIdError && userId && (
                                    <div className="msg">
                                        <small style={{ color: "green", fontSize: "10px" }}>
                                            {msgId[2]}
                                        </small>
                                    </div>
                                )

                                // value={userId}  setUserId 
                                //   !

                                // onChange={changeUserId}
                                // -> change  changeUserId !
                            }
                        </li>
                        <li>
                            {/* 2. */}
                            <label><img src="./images/svg/password.svg" alt=""/></label>
                            <input
                                type="password"
                                maxLength="20"
                                placeholder="Please enter your Password"
                                value={pwd}
                                onChange={changePwd}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                pwdError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            Password must be at least 8 characters long and must
                                            contain at least one letter and one number each.
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            {/* 3. */}
                            <label><img src="./images/svg/password.svg" alt=""/></label>
                            <input
                                type="password"
                                maxLength="20"
                                placeholder="Please check the password"
                                value={chkPwd}
                                onChange={changeChkPwd}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                chkPwdError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            Password verification does not match
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            {/* 4. */}
                            <label><img src="./images/svg/id.svg" alt=""/></label>
                            <input
                                type="text"
                                maxLength="20"
                                placeholder="Please enter your Name"
                                value={userName}
                                onChange={changeUserName}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                userNameError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            This is a required entry
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            {/* 5. */}
                            <label><img src="./images/svg/id.svg" alt=""/></label>
                            <input
                                type="text"
                                maxLength="50"
                                placeholder="Please enter your Email"
                                value={email}
                                onChange={changeEmail}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                emailError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            Please enter a valid email format
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li style={{ overflow: "hidden" }}>
                            {/* 6. */}
                            <button className="sbtn" onClick={onSubmit}>
                                Submit
                            </button>
                            {/* input submit  form
                             button submit ! */}
                        </li>
                        <li>
                            {/* 7. */}
                            Are you already a member?
                            <Link to="/login"> Log In </Link>
                        </li>
                    </ul>
                </form>
            </section>

            {/*   JS */}
            {jqFn()}
        </div>
    );
}

export default Member;