//    
import React, { useEffect, useState } from "react";
import "./css/news.css";
/*  */
import $ from "jquery";


//  
import news_data from "./data/news";
import { Link } from "react-router-dom";


//    /////////
function jqFn() {
    $(() => {
        $('.shoplist li').on('click',function(){
            console.log($(this))
            $('.shoplist li').css({
                backgroundColor : 'white',
                color : 'black'
            })
            $(this).css({
                transition : '.3s',
                backgroundColor : '#333',
                color : 'white'
                
            })
        })

    }); //////// jQB ///////////
} ////////////// jQFn ///////////


// ul>li{ ...food   ......... }

const News = () => {


    
    //  
    const ndt = news_data;
    // console.log(ndt);

    let [num,setNum] = useState('event') 
    let [ui, setUi] = useState(ndt)
    console.log('ui',ui)
    useEffect(()=>{
        let copy = [...ui]
                console.log("",ui)
                copy =ndt.filter(x => x.alignment == num)
                setUi(copy)
                console.log("",ui)
    },[num])   // num   
    
    


    return(
        <>
        
            <section className="subpage news_sec">
            {/* 1.    */}
            <h2 className="shoptit">
                #Shop Now
                <span>   ?</span>
            </h2>

            <ul className="shoplist">
                <li onClick={()=>{setNum('event')}}>  </li>
                <li onClick={()=>{setNum('trendy')}}>&#91;pop-up&#93;   </li>
                <li onClick={()=>{setNum('food')}}>   Food AVENUE</li>
                <li onClick={()=>{setNum('issue')}}>&#91;FASHION&#93;  </li>
            </ul>

            {/* 2.    */}

                <div className="news_bottom">

                    {
                        ui.map((v,i)=>
                                <Link to="/newsdetail" key={i}
                                state={{
                                    newname:v.newname,
                                    newdate:v.newdate,
                                    newdesc:v.newdesc,
                                    newstore:v.newstore
                                    }}>
                                <div className="newsbox" >
                                    <div className="nbx_img">
                                        <img src={v.newsrc} alt={v.newname} />
                                    </div>
                                    <div className="nbx_txt">
                                        <h3>
                                            <span>{v.newname.split('^')[0]}</span>
                                            {v.newname.split('^')[1]}
                                        </h3>
                                        <div className="store">{v.newstore}</div>
                                        <div className="date">{v.newdate}</div>
                                    </div>
                                </div>
                            </Link>
                        )
                    }
                </div>
            </section>
            {jqFn()}
        </>
    );

}; /////////////////// News /////////////////

export default News;