///  Gallery  - Gallery.js
import $ from "jquery";
import "../css/parallax.css";
// import gallery_data from "../data/gallery";
import { gallery_data } from "../data/gallery";
import { Parallax } from 'react-scroll-parallax';
import { useState } from "react";
import { Link } from "react-router-dom";

//    /////////
function jqFn() {
    $(() => {}); //////// jQB ///////////
} ////////////// jQFn ///////////

function Gallery(props) {
    console.log(gallery_data)
    //   
    const [by, setBy] = useState(gallery_data)
    //    
    let copy = [...by]
    //     
    let copy1;
    copy1 = [copy[0],copy[1],copy[2]]
    let copy2;
    copy2 = [copy[3],copy[4],copy[5]]

    console.log(copy1,copy2)


    //   ?
    // setBy(copy)

    console.log('',copy)
    

    // props.pg -  
    // propt.tit - 
    return (
        <>
        <div className="gal_paral">
        <Parallax speed={-60}>
          <ul className="gal_leftbox" >
          {copy1.map((v, i) => (
            <li key={i}>
            <Link to="/gallerydetail" state={{
                galname:v.galname,
                galsrc:v.galsrc,
                galperiod:v.galperiod,
                galdate:v.galdate,
                galstore:v.galstore,
                galauthor:v.galauthor,
                }}>
                
                <div className="gal_imgbx">
                <img src={v.galsrc} alt={v.galname} />
                </div>
                <div className="gal_txtbx">
                    <h3>{v.galname}</h3>
                    <div className="gal_store">{v.galstore}</div>
                    <div className="gal_period">{v.galperiod}</div>
                </div>
            </Link>
            </li>
            ))}
          </ul>
          </Parallax>

          <Parallax speed={60}>
          <ul className="gal_leftbox" >
          {copy2.map((v, i) => (
            <li key={i}>
            <Link to="/gallerydetail" state={{
                galname:v.galname,
                galsrc:v.galsrc,
                galperiod:v.galperiod,
                galdate:v.galdate,
                galstore:v.galstore,
                galauthor:v.galauthor,
                }}>
                
                <div className="gal_imgbx">
                <img src={v.galsrc} alt={v.galname} />
                </div>
                <div className="gal_txtbx">
                    <h3>{v.galname}</h3>
                    <div className="gal_store">{v.galstore}</div>
                    <div className="gal_period">{v.galperiod}</div>
                </div>
            </Link>
            </li>
            ))}
          </ul>
          </Parallax>

        </div>
           
        </>
    );
} /////////////////// Gallery //////////////

export default Gallery;