import { useEffect } from "react";
import { useLocation } from "react-router-dom";

//    
//   <BrowserRouter> 
// <ScrollTop />   !(import !)
//  PJ index.js !

export default function ScrollTop(props){ // props.sfn - 
    //      
    const { pathname } = useLocation();

    //      
    //      
    //   ? useEffect!
    // useEffect(,[]) ->  !
    useEffect(()=>{
        //     
        window.scrollTo(0,0);
        //   ! : "minfo"
        console.log("useEffect:",
        localStorage.getItem("minfo"));
        // (Layout)   !
        props.sfn();
        //   "minfo" null   
        //   (+ )
    }, [pathname]);

    // console.log(pathname);

    //      
    //    null 
    return null;
} 