//    - LogIn.js

//  
import { useState } from "react";
import "./css/member.css";
import { clearData, initData } from "./fns/fnMem"; 
import $ from 'jquery';
import { useNavigate } from "react-router-dom";

export default function LogIn() {
    // 
    let goRoute = useNavigate();

    // [  useState   ]
    // [ 1.   ]
    // 1. 
    const [userId, setUserId] = useState("");
    // 2. 
    const [pwd, setPwd] = useState("");

    // [ 2.   ]
    // ->  :   (false)
    // 1. 
    const [userIdError, setUserIdError] = useState(false);
    // 2. 
    const [pwdError, setPwdError] = useState(false);
    // [    ]
    const msgTxt = [
        "This is a required entry",//
        "ID does not exist",//  
        "Password doesn't match",//  
    ];
    //  
    // 
    const [idMsg, setIdMsg] = useState(msgTxt[0]);
    // 
    const [pwdMsg, setPwdMsg] = useState(msgTxt[0]);

    // [ 3.    ]
    // 1.    
    const changeUserId = (e) => {
        // 1.  
        if (e.target.value !== "") setUserIdError(false);
        else {
            setIdMsg(msgTxt[0]);
            setUserIdError(true);
        }

        // 2.  
        setUserId(e.target.value);
    }; /////////////// changeUserId ////////////////

    // 2.   
    const changePwd = (e) => {
        // 1.  
        if (e.target.value !== "") setPwdError(false);
        else {
            setPwdMsg(msgTxt[0]);
            setPwdError(true);
        }

        // 2.  
        setPwd(e.target.value);
    }; ///////////// changePwd ///////////////////

    // 3.     /////////////
    const totalValid = () => {
        //   (     !)
        if (!userId) setUserIdError(true);
        if (!pwd) setPwdError(true);

        // :
        // 1.  
        // 2.     false
        //  2  treu 
        if (
            userId &&
            pwd &&
            !userIdError &&
            !pwdError 
        )
            return true;
        else return false; //    false !
    }; ////////////// totalValid ////////////////

    // 7.   ///////////////
    const onSubmit = (e) => {
        //   !
        e.preventDefault();

        console.log("!");

        //    ////
        if (totalValid()) {            
            console.log("!");
            //   
            initData();

            //  "mem-data"  
            let memData = localStorage.getItem("mem-data");
            console.log(memData); 
            
            //   
            memData = JSON.parse(memData);
            console.log(memData); 

            //    
            let isOK = true;

            //   
            memData.forEach(v=>{
                //   ?
                if(v["uid"]===userId){
                    console.log(" ~~!");
                    //   
                    setUserIdError(false);

                    //     
                    isOK = false;

                    //  ?
                    if(v["pwd"]===pwd){
                        console.log(" ~~!^^")
                        //   
                        setPwdError(false);
                        $(".sbtn").text("~!");
                        // [   ]
                        // 1.    (!)
                        // ->     !
                        localStorage.setItem("minfo",JSON.stringify(v));
                        console.log(localStorage.getItem("minfo"));
                        // 2.   (useNavigate)
                        goRoute('/'); //  !
                    }
                    else{
                        console.log("!.");
                        //    
                        setPwdMsg(msgTxt[2]);
                        //   
                        setPwdError(true);
                    }
                } ////////// if ///////
            }); //////////// forEach //////////

            //   
            if(isOK){
                console.log(" !.");
                //    
                setIdMsg(msgTxt[1]);
                //   
                setUserIdError(true);
            }

        } /// if ////
        //  ////////////////
        else {
            console.log("!");
            setIdMsg(msgTxt[0]);
            setPwdMsg(msgTxt[0]);
        } /// else /////
    }; ///////////// onSubmit ////////////////

    return (
        <div className="outbx">
            {/*  */}
            <section className="membx" style={{minHeight:"300px"}}>
                <h2 onClick={clearData}><img src="./images/svg/l-point.svg" alt="lottemember"/></h2>
                <form method="post" action="process.php">
                    <ul>
                        <li>
                            {/* 1. */}
                            <label><img src="./images/svg/id.svg" alt=""/></label>
                            <input
                                type="text"
                                maxLength="20"
                                placeholder=""
                                value={userId}
                                onChange={changeUserId}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                //   idMsg !
                                userIdError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            {idMsg}
                                        </small>
                                    </div>
                                )
                            }

                            
                        </li>
                        <li>
                            {/* 2. */}
                            <label><img src="./images/svg/password.svg" alt=""/></label>
                            <input
                                type="password"
                                maxLength="20"
                                placeholder=""
                                value={pwd}
                                onChange={changePwd}
                            />
                            {
                                //    
                                //  &&  ->  true 
                                pwdError && (
                                    <div className="msg">
                                        <small style={{ color: "red", fontSize: "10px" }}>
                                            {pwdMsg}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li style={{ overflow: "hidden" }}>
                            {/* 6. */}
                            <button className="sbtn" onClick={onSubmit}>
                                LOG IN
                            </button>
                            {/* input submit  form
                             button submit ! */}
                        </li>
                    </ul>
                </form>
            </section>
        </div>
    );
} ///////////// LogIn  /////////////////