import React from "react";

const Logo = (props) => {
    // props.gb : ,

    // :     !
    const mystyle = {
        top: {
            width: "170px"
        },
        bottom: {
            width: "170px"
        },
    };

    let istyle = {
        width: "100%",
        objectFit: "cover"
    };


    //  
    const nayana = (x) => {
        //       !!
        props.tt(x);
    }; ////////////// nayana ///////////////

    return (
        <h1 style={mystyle[props.gb]} onClick={()=>nayana('!')} className="logo">
            {/* <img src={"./images/svg/logo.svg"} style={{ width: istyle[props.gb] }} /> */}
            <img src={props.gb == "bottom" ? "./images/svg/logo-white.svg" : "./images/svg/logo.svg"} style={{ width: istyle[props.gb] }} />
        </h1>
    );
}; //////////// Logo ////////////////

export default Logo;