//   
import Logo from "./Logo";
import "./css/layout.css";
import { Link, Outlet } from "react-router-dom";
import { gnb_data, foot_up, foot_down } from "./data/common";
import $ from "jquery";

import { useState } from "react";
import ScrollTop from "./common/ScrollTop";
/**************************************************************
    [       ]
    <Link to ="/"></Link>
    -> to  <Route path="/">  !

    2. <Outlet />
    ->     
**************************************************************/




const Layout = () => {

    //     
    const callMe = (x) => {
        console.log("?",x);
    }; ////////// callMe /////////////

    //    Hook :  "minfo" !
    const [logSts,setLogSts] = useState(localStorage.getItem("minfo"));
    //   Hook
    const [logMsg,setLogMsg] = useState("");
    //   
    const logstyle = {
        // position:"absolute",
        // left:"50%",
        // transform:"translateX(-50%)"
        width:"100%",
        textAlign: "center",
        height: "20px",
        paddingBottom:  "10px",
        fontSize: "12px",
        fontWeight: "800",
        color: "#009bfa"


    }; //////// logstyle ///////////

    //    //////////
    // -> ScrollTop.js  useEffect  !
    const setLogin = () => {
        // 1.  Hook 
        setLogSts(localStorage.getItem("minfo"));

        // 2.  null   
        if(localStorage.getItem("minfo")){
            //   :  "unm" !
            setLogMsg(
            JSON.parse(localStorage.getItem("minfo"))["unm"] + ",    !");
        } ////////////// if ///////////////


    }; ///////// setLogin ////////////

    //   ////////////////////////
    // -> LOGOUT  !
    const logout = () => {

        // 1.  "minfo" 
        localStorage.removeItem("minfo");

        // 2.  Hook  
        setLogSts(null);
        console.log("!",logSts);

    }; ////////////////////// logout ///////////////


    //    _______________________________________________
    function loadFn() {
        $(()=>{
    
            /***************************************************
                    HEADER  -  /
                ***************************************************/
                // 
                const viewBtn = document.querySelector(".ham");
                const closeBtn = document.querySelector(".close");
                // console.log("?",viewBtn);
    
                //   //////
                viewBtn.onclick = menuView;
                function menuView(){
                    // 1. 
                    console.log("!");
                    
                    // 2.  : .momenu
                    const menuBox = document.querySelector(".momenu");
                    
                    // 3.  :  
                    menuBox.style.top = "75px";
                    
                    
                } /////////////////// menuView  ///////////////
    
                //   //////
                closeBtn.onclick = menuClose;
                function menuClose(){
                    // 1. 
                    console.log("!");
                    
                    // 2.  : .momenu
                    const menuBox = document.querySelector(".momenu");
                    
                    // 3.  :  
                    menuBox.style.top = "-500px";
                    
                } /////////////////// menuView  ///////////////
    
    
    
        });
    
    
    } ////////////////loadFn ///////////////////////////////////

    return(
        <>
            {/*     !
            +  !  setLogin */}
            <ScrollTop sfn={setLogin} />

            {/* 1.  */}
            <header className="top">

                {/*   : -logSts null  */}
                {
                    logSts !== null &&
                    <div className="logmsg" style={logstyle}>
                        {logMsg}
                    </div>
                }

                {/* PC -   */}
                <nav className="pcmenu">

                    <Link to="/">
                        <Logo gb="top" tt={callMe} />
                    </Link>

                    <ul className="menu_list">

                        {gnb_data.map((v, i) => (
                            <li key={i}>
                                <Link to={v.link}>{v.txt}</Link>
                            </li>
                        ))}
                    </ul>

                    <ul className="menu_login">
                        {
                            /* ,    */
                            logSts === null &&
                            <>
                                <li>
                                    <Link to="/mem">Join Us</Link>
                                </li>
                                <li>
                                    <Link to="/login">LOGIN</Link>
                                </li>
                            </>
                        }

                        {
                            /*    */
                            logSts !== null &&
                            <li>
                                <a href="#" onClick={logout}>
                                    LOGOUT
                                </a>
                            </li>
                        }
                    </ul>
                    {/* <!--   --> */}
                    <div className="ham">
                        <div className="ham_top"></div>
                        <div className="ham_bottom"></div>
                    </div>
                </nav>

                {/*  -   */}
                <nav className="momenu">
        
                    <button className="close">
                        <div className="close_top"></div>
                        <div className="close_bottom"></div>
                    </button>
                    <div className="momenubox">
                        <ul className="momenu_list">
                            {gnb_data.map((v, i) => (
                                <li key={i}>
                                    <Link to={v.link}>{v.txt}</Link>
                                </li>
                            ))}
                        </ul>
                        <ul className="momenu_login">
                            {
                                /* ,    */
                                logSts === null &&
                                <>
                                    <li>
                                        <Link to="/mem">Join Us</Link>
                                    </li>
                                    <li>
                                        <Link to="/login">LOGIN</Link>
                                    </li>
                                </>
                            }

                            {
                                /*    */
                                logSts !== null &&
                                <li>
                                    <a href="#" onClick={logout}>
                                        LOGOUT
                                    </a>
                                </li>
                            }
                        </ul>
                    </div>
                </nav>

            </header>
            {loadFn()}


            {/* 2.  */}
            <main className="cont">
                {/*  :     */}
                <Outlet />
            </main>


            {/* 3. */}
            <footer className="info">

                <Link to="/main">
                    <Logo gb="bottom" tt={callMe} />
                </Link>

              <ul className="foot_up">
                {foot_up.map((v, i) => (
                    <li key={i}>
                        <a href={v.link} target="_blank">
                            {v.txt.toUpperCase()}
                        </a>
                    </li>
                ))}
              </ul>

              <ul className="foot_down">
                {foot_down.map((v, i) => (
                    <li key={i}>
                        <a href={v.link} target="_blank">
                            <img src={v.src} alt={v.txt} />
                        </a>
                    </li>
                ))}
              </ul>

            </footer>
        </>
    );
}; ///////////////////// Layout  //////////////

//  
export default Layout;